'use client';

import React, { useState } from 'react';
import './login.css';

export default function HomePage() {
  const [showLogin, setShowLogin] = useState(false); // Toggle between login and register
  const [formData, setFormData] = useState({
    fullName: '',
    lastName: '',
    email: '',
    username: '',
    password: '',
  });
  const [message, setMessage] = useState(''); // State to hold error/success message

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({ ...prevFormData, [name]: value }));
  };

  const clearForm = () => {
    setFormData({
      fullName: '',
      lastName: '',
      email: '',
      username: '',
      password: '',
    });
  };

  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault();
    clearForm(); // Clear form inputs immediately when the button is clicked
    setMessage(''); // Clear any previous message

    try {
      const response = await fetch('/api/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData),
      });

      const data = await response.json();

      if (response.ok) {
        setMessage(data.message); // Display success message
      } else {
        setMessage(data.error); // Display error message
      }
    } catch (error) {
      console.error('Error registering user:', error);
      setMessage('Something went wrong. Please try again.');
    }
  };

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    clearForm(); // Clear form inputs immediately when the button is clicked
    setMessage(''); // Clear any previous message

    try {
      const response = await fetch('/api/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ username: formData.username, password: formData.password }),
      });

      const data = await response.json();

      if (response.ok) {
        setMessage(data.message); // Display success message
      } else {
        setMessage(data.error); // Display error message
      }
    } catch (error) {
      console.error('Error logging in:', error);
      setMessage('Something went wrong. Please try again.');
    }
  };

  const handleToggle = () => {
    setShowLogin(!showLogin); // Switch between login and register
    setMessage(''); // Clear any previous message
    clearForm(); // Clear form inputs when toggling
  };

  return (
    <div className="auth-container">
      {!showLogin ? (
        <div className="form-container">
          <h2>Register</h2>
          <form onSubmit={handleRegister}>
            <label>Full Name:</label>
            <input
              type="text"
              name="fullName"
              placeholder="Enter Full Name..."
              required
              value={formData.fullName}
              onChange={handleChange}
            />

            <label>Last Name:</label>
            <input
              type="text"
              name="lastName"
              placeholder="Enter Last Name..."
              required
              value={formData.lastName}
              onChange={handleChange}
            />

            <label>Email:</label>
            <input
              type="email"
              name="email"
              placeholder="Enter Email..."
              required
              value={formData.email}
              onChange={handleChange}
            />

            <label>Password:</label>
            <input
              type="password"
              name="password"
              placeholder="Enter Password..."
              required
              value={formData.password}
              onChange={handleChange}
            />

            <label>Username:</label>
            <input
              type="text"
              name="username"
              placeholder="Enter Username..."
              required
              value={formData.username}
              onChange={handleChange}
            />

            <button type="submit" className="form-button">
              Register
            </button>
          </form>
          {message && <label className="form-message">{message}</label>}
          <p>
            Already have an account?{' '}
            <button className=" toggle-button" onClick={handleToggle}>
              Login
            </button>
          </p>
        </div>
      ) : (
        <div className="form-container">
          <h2>Login</h2>
          <form onSubmit={handleLogin}>
            <label>Username:</label>
            <input
              type="text"
              name="username"
              placeholder="Enter Username..."
              required
              value={formData.username}
              onChange={handleChange}
            />

            <label>Password:</label>
            <input
              type="password"
              name="password"
              placeholder="Enter Password..."
              required
              value={formData.password}
              onChange={handleChange}
            />

            <button type="submit" className="form-button">
              Login
            </button>
          </form>
          {message && <label className="form-message">{message}</label>}
          <p>
            New User?{' '}
            <button className="toggle-button" onClick={handleToggle}>
              Register
            </button>
          </p>
        </div>
      )}
    </div>
  );
}